import {useCallback, useEffect, useRef, useState} from 'react';
import {gsap} from 'gsap';
import {throttle} from '../utils/throttle';
// NOTE: Using non-cryptographic random for visual effects only (particles, animations)
import {random} from '../utils/random';

const DEFAULT_PARTICLE_COUNT = 12;
const DEFAULT_SPOTLIGHT_RADIUS = 300;
const DEFAULT_GLOW_COLOR = '132, 0, 255';
const MOBILE_BREAKPOINT = 768;

const createParticleElement = (x, y, color = DEFAULT_GLOW_COLOR) => {
    const el = document.createElement('div');
    el.className = 'particle';
    el.style.cssText = `
    position: absolute;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background: rgba(${color}, 1);
    box-shadow: 0 0 6px rgba(${color}, 0.6);
    pointer-events: none;
    z-index: 100;
    left: ${x}px;
    top: ${y}px;
  `;
    return el;
};

const calculateSpotlightValues = radius => ({
    proximity: radius * 0.5,
    fadeDistance: radius * 0.75
});

const ParticleCard = ({
                          children,
                          className = '',
                          disableAnimations = false,
                          style,
                          particleCount = DEFAULT_PARTICLE_COUNT,
                          glowColor = DEFAULT_GLOW_COLOR,
                          enableTilt = true,
                          clickEffect = false,
                          enableMagnetism = false
                      }) => {
    const cardRef = useRef(null);
    const particlesRef = useRef([]);
    const timeoutsRef = useRef([]);
    const isHoveredRef = useRef(false);
    const memoizedParticles = useRef([]);
    const particlesInitialized = useRef(false);
    const magnetismAnimationRef = useRef(null);

    const initializeParticles = useCallback(() => {
        if (particlesInitialized.current || !cardRef.current) return;

        const {width, height} = cardRef.current.getBoundingClientRect();
        
        // Pre-create particle elements but don't append them yet
        const particles = Array.from({length: particleCount}, () => {
            const particle = createParticleElement(random() * width, random() * height, glowColor);
            // Hide particles initially with CSS transform instead of display:none
            particle.style.transform = 'scale(0)';
            particle.style.opacity = '0';
            particle.style.pointerEvents = 'none';
            return particle;
        });
        
        // Append all particles at once to reduce DOM manipulation
        if (cardRef.current) {
            particles.forEach(particle => {
                cardRef.current.appendChild(particle);
            });
        }
        
        memoizedParticles.current = particles;
        particlesInitialized.current = true;
    }, [particleCount, glowColor]);

    const clearAllParticles = useCallback(() => {
        timeoutsRef.current.forEach(clearTimeout);
        timeoutsRef.current = [];
        magnetismAnimationRef.current?.kill();

        // Animate particles out but keep them in DOM for reuse
        particlesRef.current.forEach(particle => {
            gsap.killTweensOf(particle);
            gsap.to(particle, {
                scale: 0,
                opacity: 0,
                x: 0,
                y: 0,
                rotation: 0,
                duration: 0.3,
                ease: 'back.in(1.7)'
            });
        });
        particlesRef.current = [];
    }, []);

    const animateParticles = useCallback(() => {
        if (!cardRef.current || !isHoveredRef.current) return;

        if (!particlesInitialized.current) {
            initializeParticles();
        }

        // Use pre-created particles with CSS transforms instead of cloning
        memoizedParticles.current.forEach((particle, index) => {
            const timeoutId = setTimeout(() => {
                if (!isHoveredRef.current || !cardRef.current) return;
                
                // Reset particle position if it was already animated
                const wasAnimated = particlesRef.current.includes(particle);
                if (!wasAnimated) {
                    particlesRef.current.push(particle);
                }

                // Animate existing particle using CSS transforms
                gsap.fromTo(particle, {
                    scale: 0,
                    opacity: 0,
                    x: 0,
                    y: 0,
                    rotation: 0
                }, {
                    scale: 1,
                    opacity: 1,
                    duration: 0.3,
                    ease: 'back.out(1.7)',
                    immediateRender: wasAnimated
                });

                gsap.to(particle, {
                    x: (random() - 0.5) * 100,
                    y: (random() - 0.5) * 100,
                    rotation: random() * 360,
                    duration: 2 + random() * 2,
                    ease: 'none',
                    repeat: -1,
                    yoyo: true
                });

                gsap.to(particle, {
                    opacity: 0.3,
                    duration: 1.5,
                    ease: 'power2.inOut',
                    repeat: -1,
                    yoyo: true
                });
            }, index * 100);

            timeoutsRef.current.push(timeoutId);
        });
    }, [initializeParticles]);

    useEffect(() => {
        if (disableAnimations || !cardRef.current) return;

        const element = cardRef.current;

        const handleMouseEnter = () => {
            isHoveredRef.current = true;
            animateParticles();

            if (enableTilt) {
                gsap.to(element, {
                    rotateX: 5,
                    rotateY: 5,
                    duration: 0.3,
                    ease: 'power2.out',
                    transformPerspective: 1000
                });
            }
        };

        const handleMouseLeave = () => {
            isHoveredRef.current = false;
            clearAllParticles();

            if (enableTilt) {
                gsap.to(element, {
                    rotateX: 0,
                    rotateY: 0,
                    duration: 0.3,
                    ease: 'power2.out'
                });
            }

            if (enableMagnetism) {
                gsap.to(element, {
                    x: 0,
                    y: 0,
                    duration: 0.3,
                    ease: 'power2.out'
                });
            }
        };

        const handleMouseMoveRaw = e => {
            if (!enableTilt && !enableMagnetism) return;

            const rect = element.getBoundingClientRect();
            const x = e.clientX - rect.left;
            const y = e.clientY - rect.top;
            const centerX = rect.width / 2;
            const centerY = rect.height / 2;

            if (enableTilt) {
                const rotateX = ((y - centerY) / centerY) * -10;
                const rotateY = ((x - centerX) / centerX) * 10;

                gsap.to(element, {
                    rotateX,
                    rotateY,
                    duration: 0.1,
                    ease: 'power2.out',
                    transformPerspective: 1000
                });
            }

            if (enableMagnetism) {
                const magnetX = (x - centerX) * 0.05;
                const magnetY = (y - centerY) * 0.05;

                magnetismAnimationRef.current = gsap.to(element, {
                    x: magnetX,
                    y: magnetY,
                    duration: 0.3,
                    ease: 'power2.out'
                });
            }
        };
        
        // Throttle mouse move to ~60fps
        const handleMouseMove = throttle(handleMouseMoveRaw, 16);

        const handleClick = e => {
            if (!clickEffect) return;

            const rect = element.getBoundingClientRect();
            const x = e.clientX - rect.left;
            const y = e.clientY - rect.top;

            const maxDistance = Math.max(
                Math.hypot(x, y),
                Math.hypot(x - rect.width, y),
                Math.hypot(x, y - rect.height),
                Math.hypot(x - rect.width, y - rect.height)
            );

            const ripple = document.createElement('div');
            ripple.style.cssText = `
        position: absolute;
        width: ${maxDistance * 2}px;
        height: ${maxDistance * 2}px;
        border-radius: 50%;
        background: radial-gradient(circle, rgba(${glowColor}, 0.4) 0%, rgba(${glowColor}, 0.2) 30%, transparent 70%);
        left: ${x - maxDistance}px;
        top: ${y - maxDistance}px;
        pointer-events: none;
        z-index: 1000;
      `;

            element.appendChild(ripple);

            gsap.fromTo(
                ripple,
                {
                    scale: 0,
                    opacity: 1
                },
                {
                    scale: 1,
                    opacity: 0,
                    duration: 0.8,
                    ease: 'power2.out',
                    onComplete: () => ripple.remove()
                }
            );
        };

        element.addEventListener('mouseenter', handleMouseEnter);
        element.addEventListener('mouseleave', handleMouseLeave);
        element.addEventListener('mousemove', handleMouseMove);
        element.addEventListener('click', handleClick);

        return () => {
            isHoveredRef.current = false;
            element.removeEventListener('mouseenter', handleMouseEnter);
            element.removeEventListener('mouseleave', handleMouseLeave);
            element.removeEventListener('mousemove', handleMouseMove);
            element.removeEventListener('click', handleClick);
            clearAllParticles();
        };
    }, [animateParticles, clearAllParticles, disableAnimations, enableTilt, enableMagnetism, clickEffect, glowColor]);

    return (
        <div
            ref={cardRef}
            className={`${className} relative overflow-hidden`}
            style={{...style, position: 'relative', overflow: 'hidden'}}
        >
            {children}
        </div>
    );
};

const GlobalSpotlight = ({
                             gridRef,
                             disableAnimations = false,
                             enabled = true,
                             spotlightRadius = DEFAULT_SPOTLIGHT_RADIUS,
                             glowColor = DEFAULT_GLOW_COLOR
                         }) => {
    const spotlightRef = useRef(null);
    const isInsideSection = useRef(false);
    const pendingUpdateRef = useRef(null);

    useEffect(() => {
        if (disableAnimations || !gridRef?.current || !enabled) return;

        const spotlight = document.createElement('div');
        spotlight.className = 'global-spotlight';
        spotlight.style.cssText = `
      position: fixed;
      width: 800px;
      height: 800px;
      border-radius: 50%;
      pointer-events: none;
      background: radial-gradient(circle,
        rgba(${glowColor}, 0.15) 0%,
        rgba(${glowColor}, 0.08) 15%,
        rgba(${glowColor}, 0.04) 25%,
        rgba(${glowColor}, 0.02) 40%,
        rgba(${glowColor}, 0.01) 65%,
        transparent 70%
      );
      z-index: 200;
      opacity: 0;
      transform: translate(-50%, -50%);
      mix-blend-mode: screen;
    `;
        document.body.appendChild(spotlight);
        spotlightRef.current = spotlight;

        // Batch DOM reads to avoid forced reflows
        const processMouseMove = (e) => {
            if (!spotlightRef.current || !gridRef.current) return;

            const section = gridRef.current.closest('.bento-section');
            
            // Batch all DOM reads first
            const rect = section?.getBoundingClientRect();
            const mouseInside =
                rect && e.clientX >= rect.left && e.clientX <= rect.right && e.clientY >= rect.top && e.clientY <= rect.bottom;

            isInsideSection.current = mouseInside || false;
            const cards = Array.from(gridRef.current.querySelectorAll('.card'));

            if (!mouseInside) {
                // Cancel pending update if mouse leaves
                if (pendingUpdateRef.current) {
                    cancelAnimationFrame(pendingUpdateRef.current);
                    pendingUpdateRef.current = null;
                }
                // Batch style writes
                requestAnimationFrame(() => {
                    gsap.to(spotlightRef.current, {
                        opacity: 0,
                        duration: 0.3,
                        ease: 'power2.out'
                    });
                    cards.forEach(card => {
                        card.style.setProperty('--glow-intensity', '0');
                    });
                });
                return;
            }

            // Read all geometric properties first (batch reads in one frame)
            const cardData = cards.map(card => {
                const rect = card.getBoundingClientRect();
                return {
                    element: card,
                    rect: rect,
                    centerX: rect.left + rect.width / 2,
                    centerY: rect.top + rect.height / 2,
                    maxSize: Math.max(rect.width, rect.height) / 2
                };
            });

            // Then do all calculations (no DOM reads here)
            const {proximity, fadeDistance} = calculateSpotlightValues(spotlightRadius);
            let minDistance = Infinity;
            const glowUpdates = [];

            cardData.forEach(({element, centerX, centerY, maxSize}) => {
                const distance = Math.hypot(e.clientX - centerX, e.clientY - centerY) - maxSize;
                const effectiveDistance = Math.max(0, distance);

                minDistance = Math.min(minDistance, effectiveDistance);

                let glowIntensity = 0;
                if (effectiveDistance <= proximity) {
                    glowIntensity = 1;
                } else if (effectiveDistance <= fadeDistance) {
                    glowIntensity = (fadeDistance - effectiveDistance) / (fadeDistance - proximity);
                }

                glowUpdates.push({element, mouseX: e.clientX, mouseY: e.clientY, glowIntensity, radius: spotlightRadius});
            });

            // Cancel previous pending update to avoid race conditions
            if (pendingUpdateRef.current) {
                cancelAnimationFrame(pendingUpdateRef.current);
            }

            // Batch all style writes in one frame (use cached rects - no DOM reads)
            pendingUpdateRef.current = requestAnimationFrame(() => {
                // Use cached rects from cardData to avoid re-reading getBoundingClientRect
                glowUpdates.forEach(({element, mouseX, mouseY, glowIntensity, radius}, index) => {
                    const cachedRect = cardData[index].rect;
                    const relativeX = ((mouseX - cachedRect.left) / cachedRect.width) * 100;
                    const relativeY = ((mouseY - cachedRect.top) / cachedRect.height) * 100;

                    // Batch style updates (all writes in same frame)
                    element.style.setProperty('--glow-x', `${relativeX}%`);
                    element.style.setProperty('--glow-y', `${relativeY}%`);
                    element.style.setProperty('--glow-intensity', glowIntensity.toString());
                    element.style.setProperty('--glow-radius', `${radius}px`);
                });

                gsap.to(spotlightRef.current, {
                    left: e.clientX,
                    top: e.clientY,
                    duration: 0.1,
                    ease: 'power2.out'
                });

                const targetOpacity =
                    minDistance <= proximity
                        ? 0.8
                        : minDistance <= fadeDistance
                            ? ((fadeDistance - minDistance) / (fadeDistance - proximity)) * 0.8
                            : 0;

                gsap.to(spotlightRef.current, {
                    opacity: targetOpacity,
                    duration: targetOpacity > 0 ? 0.2 : 0.5,
                    ease: 'power2.out'
                });
                pendingUpdateRef.current = null;
            });
        };

        const handleMouseMoveRaw = (e) => {
            // Process immediately (throttled externally) but batch DOM operations
            processMouseMove(e);
        };
        
        // Throttle mouse move to ~60fps
        const handleMouseMove = throttle(handleMouseMoveRaw, 16);

        const handleMouseLeave = () => {
            isInsideSection.current = false;
            gridRef.current?.querySelectorAll('.card').forEach(card => {
                card.style.setProperty('--glow-intensity', '0');
            });
            if (spotlightRef.current) {
                gsap.to(spotlightRef.current, {
                    opacity: 0,
                    duration: 0.3,
                    ease: 'power2.out'
                });
            }
        };

        document.addEventListener('mousemove', handleMouseMove);
        document.addEventListener('mouseleave', handleMouseLeave);

        return () => {
            document.removeEventListener('mousemove', handleMouseMove);
            document.removeEventListener('mouseleave', handleMouseLeave);
            // Cancel any pending updates on cleanup
            if (pendingUpdateRef.current) {
                cancelAnimationFrame(pendingUpdateRef.current);
                pendingUpdateRef.current = null;
            }
            spotlightRef.current?.parentNode?.removeChild(spotlightRef.current);
        };
    }, [gridRef, disableAnimations, enabled, spotlightRadius, glowColor]);

    return null;
};

const BentoCardGrid = ({children, gridRef}) => (
    <div
        className="bento-section grid gap-2 p-3 max-w-[54rem] select-none relative"
        style={{fontSize: 'clamp(1rem, 0.9rem + 0.5vw, 1.5rem)'}}
        ref={gridRef}
    >
        {children}
    </div>
);

// Simple card component with proper event listener cleanup
const SimpleCard = ({
    className,
    style,
    shouldDisableAnimations,
    enableTilt,
    enableMagnetism,
    clickEffect,
    glowColor,
    textAutoHide,
    card
}) => {
    const cardRef = useRef(null);

    useEffect(() => {
        const el = cardRef.current;
        if (!el || shouldDisableAnimations) return;

        const handleMouseMoveRaw = e => {
            const rect = el.getBoundingClientRect();
            const x = e.clientX - rect.left;
            const y = e.clientY - rect.top;
            const centerX = rect.width / 2;
            const centerY = rect.height / 2;

            if (enableTilt) {
                const rotateX = ((y - centerY) / centerY) * -10;
                const rotateY = ((x - centerX) / centerX) * 10;

                gsap.to(el, {
                    rotateX,
                    rotateY,
                    duration: 0.1,
                    ease: 'power2.out',
                    transformPerspective: 1000
                });
            }

            if (enableMagnetism) {
                const magnetX = (x - centerX) * 0.05;
                const magnetY = (y - centerY) * 0.05;

                gsap.to(el, {
                    x: magnetX,
                    y: magnetY,
                    duration: 0.3,
                    ease: 'power2.out'
                });
            }
        };
        
        // Throttle mouse move to ~60fps
        const handleMouseMove = throttle(handleMouseMoveRaw, 16);

        const handleMouseLeave = () => {
            if (enableTilt) {
                gsap.to(el, {
                    rotateX: 0,
                    rotateY: 0,
                    duration: 0.3,
                    ease: 'power2.out'
                });
            }

            if (enableMagnetism) {
                gsap.to(el, {
                    x: 0,
                    y: 0,
                    duration: 0.3,
                    ease: 'power2.out'
                });
            }
        };

        const handleClick = e => {
            if (!clickEffect) return;

            const rect = el.getBoundingClientRect();
            const x = e.clientX - rect.left;
            const y = e.clientY - rect.top;

            const maxDistance = Math.max(
                Math.hypot(x, y),
                Math.hypot(x - rect.width, y),
                Math.hypot(x, y - rect.height),
                Math.hypot(x - rect.width, y - rect.height)
            );

            const ripple = document.createElement('div');
            ripple.style.cssText = `
                position: absolute;
                width: ${maxDistance * 2}px;
                height: ${maxDistance * 2}px;
                border-radius: 50%;
                background: radial-gradient(circle, rgba(${glowColor}, 0.4) 0%, rgba(${glowColor}, 0.2) 30%, transparent 70%);
                left: ${x - maxDistance}px;
                top: ${y - maxDistance}px;
                pointer-events: none;
                z-index: 1000;
            `;

            el.appendChild(ripple);

            gsap.fromTo(
                ripple,
                {
                    scale: 0,
                    opacity: 1
                },
                {
                    scale: 1,
                    opacity: 0,
                    duration: 0.8,
                    ease: 'power2.out',
                    onComplete: () => ripple.remove()
                }
            );
        };

        el.addEventListener('mousemove', handleMouseMove, {passive: true});
        el.addEventListener('mouseleave', handleMouseLeave, {passive: true});
        el.addEventListener('click', handleClick);

        return () => {
            el.removeEventListener('mousemove', handleMouseMove);
            el.removeEventListener('mouseleave', handleMouseLeave);
            el.removeEventListener('click', handleClick);
        };
    }, [shouldDisableAnimations, enableTilt, enableMagnetism, clickEffect, glowColor]);

    return (
        <div ref={cardRef} className={className} style={style}>
            <div className="card__header flex justify-between gap-3 relative text-white">
                <span className="card__label text-base">{card.label}</span>
            </div>
            <div className="card__content flex flex-col relative text-white">
                <h3 className={`card__title font-normal text-base m-0 mb-1 ${textAutoHide ? 'text-clamp-1' : ''}`}>
                    {card.title}
                </h3>
                <p className={`card__description text-xs leading-5 opacity-90 ${textAutoHide ? 'text-clamp-2' : ''}`}>
                    {card.description}
                </p>
            </div>
        </div>
    );
};

const useMobileDetection = () => {
    const [isMobile, setIsMobile] = useState(false);

    useEffect(() => {
        const checkMobile = () => setIsMobile(window.innerWidth <= MOBILE_BREAKPOINT);

        checkMobile();
        // Throttle resize events to avoid excessive updates on mobile
        const throttledCheckMobile = throttle(checkMobile, 250);
        window.addEventListener('resize', throttledCheckMobile, {passive: true});

        return () => window.removeEventListener('resize', throttledCheckMobile);
    }, []);

    return isMobile;
};

const MagicBento = ({
                        cardData,
                        textAutoHide = true,
                        enableStars = true,
                        enableSpotlight = true,
                        enableBorderGlow = true,
                        disableAnimations = false,
                        spotlightRadius = DEFAULT_SPOTLIGHT_RADIUS,
                        particleCount = DEFAULT_PARTICLE_COUNT,
                        enableTilt = true,
                        glowColor = DEFAULT_GLOW_COLOR,
                        clickEffect = true,
                        enableMagnetism = true
                    }) => {
    const gridRef = useRef(null);
    const isMobile = useMobileDetection();
    // Only disable animations on mobile, not on desktop
    const shouldDisableAnimations = disableAnimations || isMobile;

    return (
        <>
            {enableSpotlight && (
                <GlobalSpotlight
                    gridRef={gridRef}
                    disableAnimations={shouldDisableAnimations}
                    enabled={enableSpotlight}
                    spotlightRadius={spotlightRadius}
                    glowColor={glowColor}
                />
            )}

            <BentoCardGrid gridRef={gridRef}>
                <div className="card-responsive grid gap-2">
                    {cardData.map((card, index) => {
                        const baseClassName = `card flex flex-col justify-between relative aspect-[4/3] min-h-[200px] w-full max-w-full p-5 rounded-[20px] border border-solid font-light overflow-hidden transition-all duration-300 ease-in-out hover:-translate-y-0.5 hover:shadow-[0_8px_25px_rgba(0,0,0,0.15)] ${enableBorderGlow ? 'card--border-glow' : ''
                        }`;

                        const cardStyle = {
                            backgroundColor: card.color || 'var(--background-dark)',
                            borderColor: 'var(--border-color)',
                            color: 'var(--white)',
                            '--glow-x': '50%',
                            '--glow-y': '50%',
                            '--glow-intensity': '0',
                            '--glow-radius': '200px'
                        };

                        if (enableStars) {
                            return (
                                <ParticleCard
                                    key={index}
                                    className={baseClassName}
                                    style={cardStyle}
                                    disableAnimations={shouldDisableAnimations}
                                    particleCount={particleCount}
                                    glowColor={glowColor}
                                    enableTilt={enableTilt}
                                    clickEffect={clickEffect}
                                    enableMagnetism={enableMagnetism}
                                >
                                    <div className="card__header flex justify-between gap-3 relative text-white">
                                        <span className="card__label text-base">{card.label}</span>
                                    </div>
                                    <div className="card__content flex flex-col relative text-white">
                                        <h3 className={`card__title font-normal text-base m-0 mb-1 ${textAutoHide ? 'text-clamp-1' : ''}`}>
                                            {card.title}
                                        </h3>
                                        <p
                                            className={`card__description text-xs leading-5 opacity-90 ${textAutoHide ? 'text-clamp-2' : ''}`}
                                        >
                                            {card.description}
                                        </p>
                                    </div>
                                </ParticleCard>
                            );
                        }

                        return (
                            <SimpleCard
                                key={index}
                                className={baseClassName}
                                style={cardStyle}
                                shouldDisableAnimations={shouldDisableAnimations}
                                enableTilt={enableTilt}
                                enableMagnetism={enableMagnetism}
                                clickEffect={clickEffect}
                                glowColor={glowColor}
                                textAutoHide={textAutoHide}
                                card={card}
                            />
                        );
                    })}
                </div>
            </BentoCardGrid>
        </>
    );
};

export default MagicBento;

